.nav {
  position: fixed;
  display: flex;
  align-items: flex-start;
  top: 3rem;
  gap: 1.5625rem;
  left: 50%;
  transform: translateX(-50%);
}


.nav-about {
  transform: translateX(-1.56rem);
}
.nav-skills {
  transform: translateX(-6.25rem);
}
.nav-projects {
  transform: translateX(-10.94rem);
}
.nav-contact {
  transform: translateX(-15.62rem);
} 

a {
  width: 3.125rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
}

.page-title {
  font-size: 0.875rem;
  color: white;
  cursor: default;
}

.nav-link {
  opacity: 0.7;
}

.nav-link:hover {
  opacity: 1;
}

.nav-link.current {
  opacity: 1;
}

@media (max-width: 1100px) {
  .nav-about,
  .nav-skills,
  .nav-projects,
  .nav-contact {
    transform: translateX(0);
  }
  .nav {
    top: calc(100vh - 5rem);
    transform: translateX(-50%);
  }
  .nav-link {
    width: 2rem;
  }
  .nav-link img {
    width: 2rem;
  }
  .page-title {
    font-size: 0.875rem;
    color: white;
  }
}

/* Safari 6.1-10.0 (not 10.1)
@media screen and (min-color-index:0) and (-webkit-min-device-pixel-ratio:0) 
{ @media {
    .nav { 
      top: calc(100vh - 20rem);
      transform: translateX(-50%);
    }
}} */

/* Safari 11+ */
@media not all and (min-resolution:.001dpcm) and (min-color-index:0) and (-webkit-min-device-pixel-ratio:0)
{ @supports (-webkit-appearance:none) and (-webkit-hyphens:none) and (-webkit-text-stroke-color: transparent) {
    .nav { 
      top: calc(100vh - 20rem);
      transform: translateX(-50%);
    }
}}
